import {
    Component,
    OnInit,
    OnDestroy,
    ViewChild,
    HostListener,
    AfterViewInit,
    Input,
    OnChanges,
    Output,
    EventEmitter,
} from "@angular/core";
import {
    Site,
    SitesService,
    PageType,
    VersionsService as VersionsPage,
    ContainerType,
    PagesService,
    PageHistory,
    Page,
    MetaType,
    Meta,
} from "../../../api";

import { Router, ActivatedRoute } from "@angular/router";

import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import {
    BlogsService,
    ArticlesService,
    VersionsService,
    ArticleType,
    Article,
    ArticleHistory,
} from "../../../api-blog";
import { ImageLibComponent } from "../../../shared/components/image-lib/image-lib.component";
import { NgForm, FormBuilder, FormGroup, FormControl } from "@angular/forms";
import { Observable } from "rxjs/Observable";
import { ImageLibService } from "../../../shared/services/image-lib.service";
import { StoreService } from "../../../shared/services/store.service";
import { BlogService } from "../../services/blog.service";
import { ComponentCanDeactivate } from "../../../shared/_guards/pending-changes.guard";
import { slug, getSrcSet } from "../../../shared/utils/utils";
import { ErrorsService } from "../../../global/services/errors.service";
import { ModulesService } from "../../../api/api/modules.service";
import { Breadcrumb } from "../../../shared/components/breadcrumb/breadcrumb.component";
import { AppService } from "../../../global/services/app.service";
import { DraftEditorComponent } from "../../../shared/components/draft-editor/draft-editor.component";
import { ACTIONS_NAV_HEADER } from "../../../shared/components/nav-header/nav-header.component";
import { PermissionService } from "../../../shared/services/permission.service";

/**
 * Page qui cre ou edit un article de blog
 * Change l'image l'image et descriptions dans les listings
 * Change le seo
 */
@Component({
    selector: "blog-edit",
    templateUrl: "./edit.component.html",
    styleUrls: ["./edit.component.scss"],
})
export class EditComponent
    implements OnInit, OnChanges, OnDestroy, ComponentCanDeactivate, AfterViewInit {
    // @HostListener("window:beforeunload") allows us to also guard against browser refresh, close, etc.
    @HostListener("window:beforeunload")
    canDeactivate(): Observable<boolean> | boolean {
        // insert logic to check if there are pending changes here;
        // returning true will navigate without confirmation
        // returning false will show a confirm dialog before navigating away
        return this.hasSaveChanges;
    }
    // tslint:disable-next-line:member-ordering
    @ViewChild(ModalComponent)
    modalMediaLib: ModalComponent;
    @ViewChild(ImageLibComponent) imgLib: ImageLibComponent;
    @ViewChild(NgForm) f1: NgForm;
    @Output() onSavedInfo: EventEmitter<any> = new EventEmitter();


    form: FormGroup;
    // Array des sites de l'user
    sites: Site[];

    // determine l'ordre du listing des sites
    listConf = {
        order: "",
    };

    // indique si les sites sont chargs
    areLoadedSites: boolean = false;

    state: any = {};

    createSite$ = new Subject();

    articleCategories = [];

    btnEditArticle = {
        label: "Editer",
        icon: { active: true, className: "fas fa-pencil-alt" },
        className: "we-btn we-btn-block we-btn-secondary we-mb-2",
    };

    btnDeleteArticle = {
        label: "Supprimer",
        icon: { active: true, className: "far fa-trash-alt" },
        className: "we-btn we-btn-block we-btn-danger",
    };


    article: Article = { title: "Nouvel Article" };

    canAdd: boolean = false;

    cfg = null;

    /**
     * config de la librairie d'image
     */

    confLibImg = {
        tabs: {
            freeImg: { active: true },
            biblio: { active: true },
            internet: { active: true },
            color: { active: false },
        },
    };

    wantAddImg = false;

    images = [];
    version: ArticleHistory = { title: "", description: "", image: "" };
    uid = {};
    mode = "Edition";
    titleSidebar = "";

    has_draft = false;
    has_loaded = false;
    loaded: boolean = false;

    hasSaveChanges = true;

    maxCategories = 10;

    page: Page = null;
    versionPage: PageHistory = null;
    titleMeta: Meta = { content: "", name: "title" };
    descMeta: Meta = { content: "", name: "description" };
    imageSocialMeta: Meta = { "content": "", "name": "social_image" };

    displayers = {
        btns: {
            saveAndPublish: false,
            unpublish: false,
        },
    };

    loaders = {
        btns: { saveAndPublish: true, save: true, unpublish: true },
        content: true,
        metas: true,
    };

    pathBlog: string = "blog/";

    pageVersionOnline: PageHistory = null;

    pathArticle = "";

    sizeArticle = 300000;
    nearLimiteSizeArticle = { firstTime: false, status: false };

    tooltips = {
        opt: { "z-index": 1035 },
        imgCover: "Cette image apparaitra dans les listings",
    };

    breadcrumbs: Breadcrumb[] = [];

    validators = [this.noDuplicateCats.bind(this)];
    @Input() urlId = null;

    constructor(
        private articleApi: ArticlesService,
        private versionApi: VersionsService,
        private blogApi: BlogsService,
        private router: Router,
        private storeService: StoreService,
        private route: ActivatedRoute,
        private imgLibService: ImageLibService,
        public blogService: BlogService,
        private siteApi: SitesService,
        private versionPageApi: VersionsPage,
        private pageApi: PagesService,
        private errorsService: ErrorsService,
        private moduleApi: ModulesService,
        public appService: AppService,
        public permService: PermissionService,

    ) {
        this.form = new FormBuilder().group({
            chips: [["chip"], []],
        });

        if (typeof this.storeService.curSite !== "undefined") {
            this.uid = this.storeService.curSite["uid"];
            let libraryUid = this.storeService.curSite["uids"].filter(
                (uid) => uid.nom === "files"
            );

        }
    }

    ngOnChanges() {
        this.loaded = false;
        if (this.urlId) {
            this.mode = "Edition";
            this.initArticle();
        }
    }

    ngOnInit() {

        this.urlId = this.route.snapshot.params.id;
        if (this.urlId) {
            this.mode = "Edition";
            this.initArticle();
        }
        this.titleSidebar =
            (this.mode === "Edition" ? "Modifier" : "Nouvel") + " article";

        this.breadcrumbs = [
            { label: "Blog", url: "articles/posts" },
            // {label: 'Articles', url: 'articles/posts'},
            { label: this.titleSidebar, url: "" },
        ];
        // this.storeService.loadFont();
    }

    ngAfterViewInit(): void {

    }

    noDuplicateCats(control: FormControl) {
        const cat = this.articleCategories.find(
            (cat) => cat.name === control.value
        );
        if (cat) {
            return {
                noDuplicateCats: true,
            };
        }

        return null;
    }


    openModalLibImg() {
        if (!this.wantAddImg) {
            this.wantAddImg = true;
        }
        this.modalMediaLib.open();
    }

    onShown(imgLib) {
        if (imgLib) {
            imgLib.reset();
        }
    }

    /**
     * Ferme la modal et renregsitre la social Image
     * @param $event 
     */
    closeModalMedia($event) {
        // console.log("$event", $event);
        const srcSet = JSON.stringify($event.img.srcs);
        let image = $event.img.src;

        if (typeof $event.img.srcOriginal !== "undefined") {
            image = $event.img.srcOriginal;
        }

        this.version["image"] = image;
        this.version["srcSet"] = srcSet;
        delete this.version["src_set"];
        this.hasSaveChanges = false;
        this.modalMediaLib.close();


    }

    saveImg() {
        // this.cd.detectChanges();
        this.imgLib.save();
    }




    initArticle() {

        this.blogService.getArticle(this.urlId, (e) => {
            this.article = e;
            this.loaded = true;
            this.articleCategories = e["categories"];
            this.checkDraft(e);
            this.getPagePost();
            this.checkDisplayers();
            this.stateLoadersBtn(false);
        });

    }

    publish(status: boolean) {
        if (this.loaders.btns.save) {
            return;
        }

        if (status) {
            this.save(true, () => {
                this.publishRoutine(status);
            });
        } else {
            this.publishRoutine(status);
        }
    }

    /**
     * Met en ligne l'article
     */
    publishRoutine(status: boolean) {
        this.stateLoadersBtn(true);
        this.article["published"] = status;
        let article = Object.assign({ categories: [] }, this.article);

        let id = article["id"];
        delete article["id"];
        delete article["article_histories"];
        delete article["slug"];
        delete article["created"];
        delete article["updated"];

        let categories = this.formatBddCats(this.articleCategories);

        article["categories"] = categories;
        this.articleApi
            .apiArticlesArticlePut(article, String(id))
            .subscribe((e) => {
                this.hasSaveChanges = true;

                this.initArticle();
            });
    }

    public onAddCategorie(item) {
        // console.log("tag added:value is " + item);
    }

    /**
     * Sauvegarde de l'article, avec ou sans publication en ligne
     */
    save(publish: boolean, onSuccess?: Function) {

        if (this.loaders.btns.save) {
            return;
        }

        this.stateLoadersBtn(true);
        const categories = this.formatBddCats(this.articleCategories);

        /**
         * edition article ayant un id
         */
        if (typeof this.version["id"] !== "undefined") {
            this.articleApi
                .apiArticlesArticlePut(
                    {
                        title: this.article.title,
                        url: this.article.url,
                        published: this.article.published,
                        categories,
                    },
                    String(this.article.id)
                )
                .subscribe((article) => {

                    console.log("article mis a jour :", article);
                    let id = this.version["id"];

                    let verTemp = Object.assign({}, this.version);
                    if (verTemp["src_set"]) {
                        verTemp["srcSet"] = verTemp["src_set"];
                    }
                    verTemp["moduleVersion"] = verTemp["module_version"];

                    delete verTemp["id"];
                    delete verTemp["updated"];
                    delete verTemp["created"];
                    delete verTemp["online"];
                    delete verTemp["module_version"];
                    delete verTemp["src_set"];
                    // console.log("verTemp !!", verTemp);

                    this.versionApi
                        .apiVersionsSlugPut(verTemp, String(id))
                        .subscribe((articleHistory) => {
                            //console.log("version a update :",this.version, articleHistory);
                            if (publish) {
                                if (typeof onSuccess === "function") {
                                    onSuccess(articleHistory);
                                    this.onSavedInfo.emit(article);
                                }
                            } else {
                                this.hasSaveChanges = true;
                                this.onSavedInfo.emit(article);
                                this.initArticle();
                            }
                        });
                });
        }
    }


    ngOnDestroy() {
        this.state = null;
    }

    formatBddCats(categories) {
        const cats = [];

        categories.map((cat, index) => (cats[index] = cat.id));

        return cats;
    }

    updatePost(data) {
        // this.cd.detectChanges();
        data.item.inEdit = false;
        if (data.name) {
            data.item.name = data.name;
        }

        const { published, url, id, categories } = data.item.model;
        const article: ArticleType = {
            title: data.item.name,
            url,
            published,
            categories: this.formatBddCats(categories),
        };
        this.blogService.putArticle(article, String(id), () => { });
    }



    /**
     * Gestion categories articles
     */
    onItemAdded(tag) {
        // console.log("item added tag", tag);
        let match_tag = {};

        if (tag.name === tag.id) {
            let match = false;

            for (let i in this.blogService.categories) {
                if (this.blogService.categories[i].name === tag.name) {
                    match = true;
                    match_tag = this.blogService.categories[i];
                }
            }

            if (match === false) {
                this.blogService.postCategory(
                    { name: tag.name },
                    (category) => {
                        for (let i in this.articleCategories) {
                            if (this.articleCategories[i].name === tag.name) {
                                this.articleCategories[i] = category;
                            }
                        }
                        this.hasSaveChanges = false;
                    }
                );
            } else {
                for (let i in this.articleCategories) {
                    if (this.articleCategories[i].name === tag.name) {
                        this.articleCategories[i] = match_tag;
                    }
                }
            }

            // console.log("match:", true);
        }
        this.hasSaveChanges = false;
    }



    goToPosts() {
        this.router.navigate(["blog"]);
    }



    /*
   Page de l'article du blog
   */
    getPagePost() {
        this.moduleApi
            .apiSitesSlugModulesModulePagesModuleResourceGet(
                String(this.storeService.curSite.id),
                this.blogService.idModuleBlog,
                String(this.article.id)
            )
            .subscribe(
                (e) => {
                    this.page = e;
                    this.pathArticle = this.page.url;
                    this.getVersionPage();
                },
                (error) => this.errorsService.handleErrorHttp(error)
            );
    }

    // Version de la page pour le SEO
    getVersionPage() {
        this.pageApi
            .apiPagesSlugVersionsLastGet(String(this.page.id))
            .subscribe((pageHistory) => {
                this.versionPage = pageHistory;
                this.titleMeta = pageHistory.metas.find(
                    (m) => m.name === "title"
                );
                this.descMeta = pageHistory.metas.find(
                    (m) => m.name === "description"
                );
            });
    }

    /**
     * Recupere un brouillon ou en crer un 
     * @param item 
     */
    getDraft(item) {

        return new Promise((resolve, reject) => {
            //console.log("retour systmatique d'une version hors-ligne ---> transform en API Call", item.id, item);

            const foundOffline = item.article_histories.find(element => element.online == false);
            if (!foundOffline) {
                this.articleApi.apiVersionsArticlePost(item.id).subscribe(b => {
                    //console.log("Clone de l'article --> utilisation version", b.id);
                    resolve(b);
                });

            } else {
                resolve(foundOffline);
            }

        });

    }



    checkDraft(article) {

        this.getDraft(article).then(e => {
            //console.log("version charg", e);
            this.version = e;
            this.has_draft = true;
            this.has_loaded = true;
        });

        /*         let drafts = Object.assign([], article["article_histories"]);
                let draftNum = drafts.filter((version) => version.online === false);
        
                // si Possde un brouillon
                if (draftNum.length === 1) {
                    this.version = article["article_histories"].filter(
                        (version) => version.online === false
                    )[0];
                    this.has_draft = true;
                    this.has_loaded = true;
                } else {
                    this.version = article["article_histories"].filter(
                        (version) => version.online === true
                    )[0];
                    this.has_draft = false;
                    this.has_loaded = true;
                } */
    }

    /**
     * Check la visibilit des boutons
     */
    checkDisplayers() {
        this.displayers.btns.saveAndPublish =
            this.has_draft === true || this.article.published === false;
        this.displayers.btns.unpublish = this.article.published === true;
    }

    stateLoadersBtn(state) {
        this.loaders.btns.save = state;
        this.loaders.btns.unpublish = state;
        this.loaders.btns.saveAndPublish = state;
    }


    updateSlug(value) {
        // this.article.url = value;
        this.pathArticle = this.pathBlog + slug(this.article.url);
    }




    deleteVersionImage() {
        this.version.image = "";
        this.hasSaveChanges = false;
    }
}



