import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { MainComponent } from "./components/main/main.component";
import { AccueilComponent } from "./components/accueil/accueil.component";
import { EditComponent } from "./components/edit/edit.component";
import { CategoriesListComponent } from "./components/categories-list/categories-list.component";
import { PendingChangesGuard } from "../shared/_guards/pending-changes.guard";
import { ApercuArticleComponent } from "./pages/apercu-article/apercu-article.component";

const routes: Routes = [
    {
        path: "",
        component: MainComponent,
        canDeactivate: [PendingChangesGuard],
        children: [
            { path: "articles", component: AccueilComponent },
            {
                path: "articles/edit/:id",
                component: EditComponent
            },
            {
                path: "articles/nouveau",
                component: EditComponent
            },
            { path: "categories", component: CategoriesListComponent },
            // { path: "apercu/article/:id", component: ApercuArticleComponent },
            { path: "apercu/article", component: ApercuArticleComponent },

            { path: "**", redirectTo: "articles" }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class BlogRoutingModule {}



