import { Component, OnInit, OnDestroy, ViewChild, OnChanges } from "@angular/core";
import { Site, ModulesService, PageType, SitesService, PagesService } from "../../../api";
import { StoreService } from "../../../shared/services/store.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import {
    BlogsService,
    ArticlePagination,
    ArticlesService,
    VersionsService,
    ArticleHistory,
} from "../../../api-blog";
import { ImageLibComponent } from "../../../shared/components/image-lib/image-lib.component";
import { BlogService } from "../../services/blog.service";
import { Router } from "@angular/router";
import { AppService } from "../../../global/services/app.service";
import { ErrorsService } from "../../../global/services/errors.service";
import { resolve } from "url";

declare var moment: any;

/**
 * Page qui liste les articles et les brouillons
 * + suppression
 */
@Component({
    selector: "blog-list",
    templateUrl: "./list.component.html",
    styleUrls: ["./list.component.scss"],
})
export class ListComponent implements OnInit, OnDestroy {

    @ViewChild(ImageLibComponent) imglib: ImageLibComponent;
    @ViewChild("modalDeletePost")
    modalDeletePost: ModalComponent;
    @ViewChild("modalInfos") modalInfos: ModalComponent;


    state: any = {};

    btnEditArticle = {
        label: "Editer",
        icon: { active: true, className: "fas fa-pencil-alt" },
        className: "we-btn we-btn-block we-btn-secondary we-mb-2",
    };

    btnDeleteArticle = {
        label: "Supprimer",
        icon: { active: true, className: "far fa-trash-alt" },
        className: "we-btn we-btn-block we-btn-danger",
    };

    articles = [];
    articles_unpublished = [];

    pagination: ArticlePagination = { items: [] };
    pagination_unpublished: ArticlePagination = { items: [] };

    pagination_loaded = false;
    pagination_unpublished_loaded = false;

    page_published = 1;
    page_unpublished = 1;

    actions = {};

    showLists = { published: true, unpublished: false };

    wantAddImg = false;
    saveArticle = false;

    ItemImageToUpdate = null;

    confLibImg = {
        tabs: {
            freeImg: { active: true },
            biblio: { active: true },
            internet: { active: true },
            color: { active: false },
        },
    };

    itemToDelete = null;
    reloadPublish = null;
    loadedList = false;
    selectedItem = {};
    inRedirect = false; // on est entrain de rediriger vers editeur

    constructor(
        private blogApi: BlogsService,
        private moduleApi: ModulesService,
        private blogService: BlogService,
        private articleApi: ArticlesService,
        private versionApi: VersionsService,
        public storeService: StoreService,
        private siteApi: SitesService,
        private pageApi: PagesService,
        private router: Router,
        private errorsService: ErrorsService,
        public appService: AppService
    ) { }

    formatDate(d) {
        return moment(d).fromNow();
    }

    ngOnInit() {
        this.getPublish();
        this.getUnPublish();
    }

    onChangePagePublish($event) {
        this.page_published = $event.page;
        this.getPublish();
    }

    onChangePageUnPublish($event) {
        this.page_unpublished = $event.page;
        this.getUnPublish();
    }

    wantDelete(item, publish) {
        this.itemToDelete = item;
        this.reloadPublish = publish;
        this.modalDeletePost.open();
    }

    deletePost() {
        const idToDelete = this.itemToDelete.id;
        this.articleApi
            .apiArticlesArticleDelete(String(idToDelete))
            .subscribe(() => {
                this.loadedList = false;
                this.getPublish();
                this.getUnPublish();
                this.blogService.nbPosts--;
                this.modalDeletePost.close();
            });
    }

    getPublish() {
        this.blogApi
            .apiBlogsSlugArticlesGet(
                this.storeService.blogId,
                this.page_published,
                1
            )
            .subscribe((e) => {
                this.pagination = e;
                let articles = e.items;
                articles.map((article) => {
                    const versions = article["article_histories"];
                    if (versions) {
                        versions.map((version) => {
                            if (version["online"] == true) {
                                article["image"] = version["image"];
                            }
                        });
                    }
                });

                this.articles = articles;
                this.pagination_loaded = true;
                this.loadedList = true;
            });
    }

    hasDraft(e) {
        if (e.filter((version) => version.online === false).length == 1) {
            return true;
        } else {
            return false;
        }
    }

    getUnPublish() {
        this.blogApi
            .apiBlogsSlugArticlesGet(
                this.storeService.blogId,
                this.page_unpublished,
                0
            )
            .subscribe((e) => {
                this.pagination_unpublished = e;
                let articles = e.items;

                articles.map((article) => {
                    const versions = article["article_histories"];
                    if (versions) {
                        versions.map((version) => {
                            if (
                                version["online"] == true ||
                                version["online"] == false
                            ) {
                                article["image"] = version["image"];
                                article["title"] = version["title"];
                            }
                        });
                    }
                });

                this.articles_unpublished = articles;
                this.pagination_unpublished_loaded = true;
                this.loadedList = true;
            });
    }

    ngOnDestroy() {
        // prevent memory leak when component destroyed
        this.state = null;
    }

    showList(type: string) {
        this.showLists.published = type === "published";
        this.showLists.unpublished = type === "unpublished";
    }


    /**
     * 
     * @param item Modifier les infos Titre/Description/Image pour les listings
     */
    modifyInfosListing(item) {
        this.selectedItem = item;
        this.modalInfos.open();
        // this.router.navigate(["/blog", "articles", "edit", item.id])
    }


    closeModalInfos($event) {
        this.modalInfos.close();
        this.loadedList = false;
        this.getPublish();
        this.getUnPublish();
    }


    roughScale(x, base) {
        const parsed = parseInt(x, base);
        if (isNaN(parsed)) { return 0; }
        return parsed;
    }


    /**
     * 
     * @param item Publie la page 
     * SI brouillon sans version online , offline --> online
     */
    publishPage(item, status) {

        this.loadedList = false;
        //console.warn("Publie article " + status + " la page associ  l'article", item);
        let statusToInt = (status) ? 1 : 0;

        //Publie sur l'Api Blog l'article et ecrase la version en ligne par le brouillon
        this.articleApi.apiArticlesArticlePublishesPost(String(item.id), statusToInt)
            .subscribe((e) => {
                //console.log("Page publish updated ", e, status);
                if (status) {
                    this.moduleApi
                        .apiSitesSlugModulesModulePagesModuleResourceGet(
                            String(this.storeService.curSite.id),
                            this.blogService.idModuleBlog,
                            String(item.id)
                        )
                        .subscribe(
                            (page) => {
                                //Publie la page sur l'Api Page 
                                this.siteApi.apiPagesPagePublishPut(String(page.id)).subscribe(e => {
                                    this.getPublish();
                                    this.getUnPublish();
                                });
                            })
                } else {
                    //pour dpublier, on fait rien le rendu de page lira le statut de l'article comme non-publi et affichera une 404
                    this.getPublish();
                    this.getUnPublish();
                }

            });
    }

    inIframe() {
        try {
            return window.self !== window.top;
        } catch (e) {
            return true;
        }
    }


    /**
     *  Redirige vers L'editeur
     * @param page la page associ a l'article dans l'editeur 
     * @param item l'article
     */
    redirectToPageEditor(page, item) {



        //refresh des listings les ids vont changer au prochain retour
        this.getPublish();
        this.getUnPublish();

        this.inRedirect = false;

        const strMsg = JSON.stringify({
            goToModule: "blog",
            goToModuleUrl: "/editeur/pages/blog/" + page.id,
            page: page,
            item: item
        });

        parent.postMessage(strMsg, "*");
    }




    /**
     * Recupere un brouillon ou en crer un 
     * @param item 
     */
    getDraft(item) {

        return new Promise((resolve, reject) => {
            //console.log("retour systmatique d'une version hors-ligne ---> transform en API Call", item.id, item);

            this.articleApi.apiArticlesArticleGet(item.id).subscribe(
                e => {
                    const foundOffline = item.article_histories.find(element => element.online == false);
                    if (!foundOffline) {
                        this.articleApi.apiVersionsArticlePost(item.id).subscribe(b => {
                            //console.log("Clone de l'article --> utilisation version", b.id);
                            resolve(b);
                        });

                    } else {
                        resolve(foundOffline);
                    }
                },
                error => {
                    reject();
                    this.errorsService.handleErrorHttp(error);
                }
            );
        });

    }


    /**
     * Action sur le bouton modifier du listing
     * @param item l'article    
     * @param status listing des articles publis ou non-publis
     */
    getPageLinkedToItem(item) {

        if (!this.inRedirect) {
            this.inRedirect = true;
            this.moduleApi
                .apiSitesSlugModulesModulePagesModuleResourceGet(
                    String(this.storeService.curSite.id),
                    this.blogService.idModuleBlog,
                    String(item.id)
                )
                .subscribe(
                    (e) => {


                        this.getDraft(item).then((brouillon: ArticleHistory) => {

                            //console.log("Redirect to Editor", "article :" + item.id + " ---> page : " + e.id,);

                            //recup la derniere version de page offline si existe
                            let historyversionToUpdate = brouillon;
                            let id = historyversionToUpdate.id;
                            delete historyversionToUpdate.id;
                            delete historyversionToUpdate['module_version']; //bug symfony camelcase
                            delete historyversionToUpdate['src_set']; //bug symfony camelcase
                            delete historyversionToUpdate.online;
                            delete historyversionToUpdate.updated;
                            delete historyversionToUpdate.created;
                            delete historyversionToUpdate.title;
                            delete historyversionToUpdate.body;
                            delete historyversionToUpdate.description;
                            delete historyversionToUpdate.image;

                            //console.log("Nouveau rendu, maj date updated ", id, historyversionToUpdate);
                            historyversionToUpdate.moduleVersion = "1";
                            this.versionApi
                                .apiVersionsSlugPatchPut(
                                    historyversionToUpdate,
                                    String(id)
                                )
                                .subscribe((b) => {
                                    this.redirectToPageEditor(e, item);
                                });

                        });


                    },
                    (error) => { }//this.errorsService.handleErrorHttp(error)
                );

        }
    }


    clickOnRowItem(event: Event, item, status) {
        const canNavigate =
            event.srcElement &&
            event.srcElement.nodeName !== "BUTTON" &&
            event.srcElement.nodeName !== "I";

        if (canNavigate) {
            this.getPageLinkedToItem(item);
            //this.router.navigate(["/blog", "articles", "edit", item.id]);
        }
    }


    /**
     * Cration de l'article,
     * puis sa version,
     * puis la page du site,
     * les metas sont cres par la modal dans l'editeur
     * puis on redirige vers la page dans l'editeur
     */
    createNewArticle() {

        if (!this.saveArticle) {
            this.saveArticle = true;

            let version: ArticleHistory = {
                "moduleVersion": "1",
                "title": "Mon Article",
                "description": "Proin vel efficitur dolor, non egestas massa. Vestibulum vitae velit diam. Fusce sollicitudin, nisl sed suscipit elementum, augue arcu venenatis tellus, vel sollicitudin tortor leo nec odio. Vivamus ullamcorper odio at velit lacinia pretium. Pellentesque sit amet odio rhoncus, viverra felis vel, ultrices sem. Nullam luctus gravida aliquet.",
                "body": "Proin vel efficitur dolor, non egestas massa. Vestibulum vitae velit diam. Fusce sollicitudin, nisl sed suscipit elementum, augue arcu venenatis tellus, vel sollicitudin tortor leo nec odio. Vivamus ullamcorper odio at velit lacinia pretium. Pellentesque sit amet odio rhoncus, viverra felis vel, ultrices sem. Nullam luctus gravida aliquet.",
                "image": "https://images.unsplash.com/photo-1516048015710-7a3b4c86be43?ixlib=rb-0.3.5&s=6aea5ce84923e1ba4653c168dd07e6c5&auto=format&fit=crop&w=1350&q=30"
            };

            let uniqid = Math.floor(Date.now() / 1000);

            //le body de la version sera remplace par une page offline
            //on verra les mic macs lors de la publication  de la page    
            this.blogApi
                .apiBlogsBlogArticlesPost(
                    {
                        title: "Mon nouvel article",
                        url: "Mon nouvel article " + uniqid,
                        categories: [],
                    },
                    this.storeService.blogId
                )
                .subscribe((e) => {
                    let article = e;
                    this.versionApi
                        .apiVersionsSlugPut(
                            version,
                            String(e["article_histories"][0].id)
                        )
                        .subscribe((b) => {
                            this.blogService.nbPosts++;
                            this.createPageArticle(article, uniqid);

                        });
                });
        }
    }


    createPageArticle(article, uniqid) {
        const page: PageType = {
            nom: "Mon article",
            // url: "blog/" + article.slug + "_" + article.id,
            url: "blog/" + article.slug,
            editor: "10", // choper lediteur sur le site
            is_home: false,
            is_error: false,
            // layout: "1",
            module: "1",
            module_resource: article.id,
            module_page_type: "article",
        };

        this.siteApi
            .apiSitesSitePagesPost(page, String(this.storeService.curSite.id))
            .subscribe(
                (e) => {
                    this.pageApi
                        .apiPagesSlugVersionsLastGet(String(e.id))
                        .subscribe(
                            (pageHistory) => {
                                this.redirectToPageEditor(e, article);
                                this.saveArticle = false;
                            },
                            (error) => {
                                this.errorsService.handleErrorHttp(error);
                            }
                        );
                },
                (error) => alert(this.errorsService.handleErrorHttp(error))
            );
    }
}



