import { Injectable } from "@angular/core";
import {
    BlogsService,
    ArticlesService,
    ArticleType,
    CategorieType
} from "../../api-blog";
import { Subject } from "rxjs/Subject";
import { StoreService } from "../../shared/services/store.service";
import { ErrorsService } from "../../global/services/errors.service";
import { ModulesService } from "../../api/api/modules.service";
import { ModuleService } from "../../global/services/module.service";
import { PermissionService } from "../../shared/services/permission.service";
import { StatsService } from "../../api-blog/api/stats.service";

@Injectable()
export class BlogService {
    // ID du module blog dans la bdd
    idModuleBlog = "1";
    articles = [];
    categories = [];

    // Observable des events blog
    events = new Subject<any>();

    labels = {
        helperTags: "Appuyer sur la touche ntre pour valider une valeur"
    };

    nbPosts = 0;

    constructor(
        private blogApi: BlogsService,
        private articleApi: ArticlesService,
        private storeService: StoreService,
        private errorsService: ErrorsService,
        private permService: PermissionService,
        private store: StoreService,
        private statsApi: StatsService
    ) {}

    getArticles(
        page_published = 1,
        published: 0 | 1 = 1,
        onSuccess?: Function,
        onError?: Function
    ) {
        this.blogApi
            .apiBlogsSlugArticlesGet(
                this.storeService.blogId,
                page_published,
                published
            )
            .subscribe(
                e => {
                    this.articles = e.items;
                    if (typeof onSuccess === "function") {
                        onSuccess(e);
                    }
                },
                error => {
                    this.errorsService.handleErrorHttp(error);
                    if (typeof onError === "function") {
                        onError(error);
                    }
                }
            );
    }

    getCategories(onSuccess?: Function, onError?: Function) {
        this.blogApi
            .apiBlogsSlugCategoriesGet(this.storeService.blogId)
            .subscribe(
                cats => {
                    this.categories = cats;
                    if (typeof onSuccess === "function") {
                        onSuccess(cats);
                    }
                },
                error => {
                    this.errorsService.handleErrorHttp(error);
                    if (typeof onError === "function") {
                        onError(error);
                    }
                }
            );
    }

    // Crer un article
    postArticle(
        articleItem: ArticleType,
        onSuccess?: Function,
        onError?: Function
    ) {
        this.blogApi
            .apiBlogsBlogArticlesPost(articleItem, this.storeService.blogId)
            .subscribe(
                e => {
                    if (typeof onSuccess === "function") {
                        onSuccess(e);
                    }
                },
                error => {
                    this.errorsService.handleErrorHttp(error);
                    if (typeof onError === "function") {
                        onError(error);
                    }
                }
            );
    }

    postCategory(tag: CategorieType, onSuccess?: Function, onError?: Function) {
        this.blogApi
            .apiBlogsBlogCategoriesPost(
                { name: tag.name },
                this.storeService.blogId
            )
            .subscribe(
                category => {
                    this.categories.push(category);
                    if (typeof onSuccess === "function") {
                        onSuccess(category);
                    }
                },
                error => {
                    this.errorsService.handleErrorHttp(error);
                    if (typeof onError === "function") {
                        onError(error);
                    }
                }
            );
    }

    getArticle(id, onSuccess?: Function, onError?: Function) {

        this.articleApi.apiArticlesArticleGet(id).subscribe(
            e => {
                if (typeof onSuccess === "function") {
                    onSuccess(e);
                }
            },
            error => {
                this.errorsService.handleErrorHttp(error);
                if (typeof onError === "function") {
                    onError(error);
                }
            }
        );
    }

    putArticle(
        articleItem: ArticleType,
        id,
        onSuccess?: Function,
        onError?: Function
    ) {
        this.articleApi
            .apiArticlesArticlePut(articleItem, String(id))
            .subscribe(
                e => {
                    if (typeof onSuccess === "function") {
                        onSuccess(e);
                    }
                },
                error => {
                    this.errorsService.handleErrorHttp(error);
                    if (typeof onError === "function") {
                        onError(error);
                    }
                }
            );
    }

    hasReachedMaxNbPosts(openModal: boolean = true) {
        const hasReached = this.permService.hasReachedLimit(
            this.permService.LIMIT.NB_POSTS_BLOG,
            this.nbPosts
        );
        if (hasReached && openModal) {
            this.permService.openModalPlan(
                this.permService.LIMIT.NB_POSTS_BLOG
            );
        }
        return hasReached;
    }

    fetchNbPosts(onSuccess: Function) {
        this.statsApi
            .apiBlogsSlugArticleTotalGet(this.store.blogId)
            .subscribe(result => {
                this.nbPosts = result;
                onSuccess();
            });
    }
}



