import {
    Component,
    OnInit,
    ViewChild,
    TemplateRef,
    AfterViewInit,
    ChangeDetectorRef
} from "@angular/core";
import { MenuItem } from "../../../shared/classes/menu";
import { BlogService } from "../../services/blog.service";
import { Router } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { ListComponent } from "../list/list.component";
import { PermissionService } from "../../../shared/services/permission.service";

declare var $: any;

@Component({
    selector: "blog-accueil",
    templateUrl: "./accueil.component.html",
    styleUrls: ["./accueil.component.scss"]
})
export class AccueilComponent implements OnInit {
    subscription: Subscription;
    showLimit = false;

    @ViewChild(ListComponent) blogList: ListComponent;

    actions = {
        refresh_list_accueil: data => this.refreshList()
    };

    constructor(
        private blogService: BlogService,
        private router: Router,
        public permService: PermissionService
    ) {}

    ngOnInit() {
        this.subscription = this.blogService.events.subscribe(
            $e => this.actions[$e.action] && this.actions[$e.action]($e)
        );
    }

    refreshList() {
        // this.blogList.getPublish();
        // this.blogList.getUnPublish();
    }

    goTo(where: string) {
        if (where === "home") {
            this.router.navigate(["/"]);
        }
    }

    goToNewPost() {
        this.showLimit = this.blogService.hasReachedMaxNbPosts();
        if (!this.showLimit) {
            this.router.navigateByUrl("/blog/articles/nouveau");
        }
    }
}



