import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { BlogService } from "../../services/blog.service";
import { ModuleService } from "../../../global/services/module.service";

/**
 * Point d'entre du blog
 * Activer le blog si necessaire
 * Recuperes les categories
 * Compte le nombre de posts
 */
@Component({
    selector: "app-main",
    templateUrl: "./main.component.html",
    styleUrls: ["./main.component.scss", "../../blog.scss"],
    encapsulation: ViewEncapsulation.None,
})
export class MainComponent implements OnInit {
    blogLoaded: boolean = false;
    catsLoaded: boolean = false;
    constructor(
        private blogService: BlogService,
        private moduleService: ModuleService
    ) {}

    ngOnInit() {
        this.moduleService.activateModule("blog").then(() => {
            this.blogService.getCategories(() => {
                this.blogService.fetchNbPosts(() => {
                    this.blogLoaded = true;
                });
            });
        });

        // this.blogService.getBlog(null, () => {
        //     this.blogService.getCategories(() => {
        //         this.blogLoaded = true;
        //     });
        // });
    }
}



